import React, { useCallback } from "react";
import { Dimensions, SafeAreaView, StyleSheet } from "react-native";
import { Assets, Button, ButtonSize, Colors, GridView, Text, Timeline, View } from "react-native-ui-lib";
import { colors } from "../../../assets/color/color";
import TimelineScreen from "../../component/Timeline";
import Header from "../../component/AppBar";
import { useCreateContract } from "../../Contexts/CreateContract";

const GuildeLine = (props: any) => {
  const { navigation, route } = props

  const handleSubmit = () => {
    props.setNavigationName('MainEkyc');
    props.setTypeContract(route?.params?.title)
  }

  const renderTimelineItem = (props: any) => {
    return (
      <Timeline
        bottomLine={props.label !== 3 ? {
          state: Timeline.states.CURRENT,
        } : undefined}
        point={{
          label: props.label,
          color: colors.primary,
        }}
        topLine={props.label !== 1 ?
          {
            type: Timeline.lineTypes.SOLID,
            state: Timeline.states.CURRENT,
          } : undefined}
      >
        <View margin-20>
          <Text br30 style={{ fontSize: 18, fontWeight: "600", color: colors.colorTitie }}>{props.title}</Text>
        </View>
      </Timeline>
    )
  }

  return (
    <SafeAreaView style={{ backgroundColor: "#fff", flex: 1 }}>
      <Header label="Tạo hồ sơ vay" {...props} />
      <Text
        style={{
          paddingVertical: 63,
          fontSize: 20,
          fontWeight: "600",
          color: colors.colorTitie,
        }}
        center>Quy trình tạo hồ sơ vay</Text>
      <View center style={{ paddingHorizontal: 75 }}>
        <GridView
          items={[
            { renderCustomItem: () => renderTimelineItem({ label: 1, title: "Xác thực thông tin" }) },
            { renderCustomItem: () => renderTimelineItem({ label: 2, title: "Nhập thông tin hồ sơ" }) },
            { renderCustomItem: () => renderTimelineItem({ label: 3, title: "Hoàn tất" }) },
          ]}
          numColumns={1}
          itemSpacing={0}
        />
      </View>
      <View style={{ flexDirection: "row", alignItems: "center", justifyContent: "space-around", flex: 1 }}>
        <Button onPress={() => navigation.goBack()} style={{ ...styles.btn, backgroundColor: "#fff", borderColor: "#E7E7E8", borderWidth: 1, borderStyle: "solid" }} labelStyle={{ ...styles.labelStyle, color: "#000A2A" }} label={'Quay lại'} size={ButtonSize.large} borderRadius={12} />
        <Button onPress={handleSubmit} style={{ ...styles.btn, backgroundColor: colors.primary }} labelStyle={{ ...styles.labelStyle, color: "#fff" }} label={'Tiếp tục'} size={ButtonSize.large} borderRadius={12} />
      </View>
    </SafeAreaView>
  )
}

const styles = StyleSheet.create({
  btn: {
    minWidth: 100
  },
  labelStyle: {
    fontSize: 16,
    fontWeight: "500"
  }
})

export default GuildeLine;