import React, { useCallback, useEffect, useState } from "react";
import { FlatList, ImageBackground, SafeAreaView, ScrollView } from "react-native";
import { GridList, GridView, View } from "react-native-ui-lib";
import InputComponent from "../InputComponent";
import ContractItemComponent from "./ContractItem";
import PickerComponent from "../PickerComponent";
import { Svg, Path } from "react-native-svg"
import Header from "../AppBar";
import { chechStatusMyContract } from "../../utlis/data";
import { useFocusEffect } from "@react-navigation/native";

const ContractComponent = (props: any) => {
  const [text, onChangeSearch] = useState('')
  const [listContract, setListContract] = useState<any>()
  const [statusContract, setStatusContract] = useState("all")
  const [statusProcessContract, setStatusProcessContract] = useState("all")
  const [statusShop, setStatusShop] = useState("all")
  const [statusProduct, setStatusProduct] = useState("all")
  const [statusDate, setStatusDate] = useState("all")

  useFocusEffect(useCallback(() => {
    const newList = chechStatusMyContract.map((item) => {
      switch (item.title) {
        case 'Trạng thái HS':
          return {
            ...item,
            value: statusContract,
            setValue: setStatusContract
          }
        case 'Trạng thái duyệt':
          return {
            ...item,
            value: statusProcessContract,
            setValue: setStatusProcessContract
          }
        case 'Đại lý':
          return {
            ...item,
            value: statusShop,
            setValue: setStatusShop
          }
        case 'Sản phẩm':
          return {
            ...item,
            value: statusProduct,
            setValue: setStatusProduct
          }
        case 'Ngày tạo':
          return {
            ...item,
            value: statusDate,
            setValue: setStatusDate
          }
        default: return;
      }
    })
    setListContract(newList);
  }, []))

  useEffect(() => {
    console.log(statusContract, statusProcessContract, statusShop, statusProduct, statusDate);
  }, [statusContract, statusProcessContract, statusShop, statusProduct, statusDate])

  const renderItemContractItem = () => {
    return (
      <ContractItemComponent />
    )
  }

  const renderStatus = ({ item }: any) => {
    return (
      <View key={item.id} marginR-15 style={{ minWidth: 130 }}>
        <PickerComponent placeholder={item.title} data={item.listDropDown} value={item.value} onChangeValue={item.setValue} />
      </View>
    )
  }

  return (
    <View style={{ flex: 1, backgroundColor: "#F8FAFE", borderRadius: 17, position: "relative" }}>
      <Header containerStyle={{
        height: "15%",
      }} backgroundColor='#F2E0FE' color={'#62248F'} label="Hồ sơ của tôi (24)" {...props} />
      <SafeAreaView
        style={{
          backgroundColor: "rgba(255, 255, 255, 1)",
          position: "absolute",
          top: "13%",
          borderTopLeftRadius: 17,
          borderTopRightRadius: 17,
          height: "88%"
        }}>

        <View marginR-30 marginL-30 marginT-20 marginB-20>
          <InputComponent placeholder="Tìm kiếm..." text={text} onChangeText={onChangeSearch} />
        </View>
        {listContract && (
          <FlatList
            data={listContract}
            renderItem={renderStatus}
            horizontal
            showsHorizontalScrollIndicator={false}
            numColumns={1}
            contentContainerStyle={{ paddingBottom: 20, paddingHorizontal: 30 }}
          />
        )}
        <GridList
          data={[{}, {}, {}, {}, {}, {}, {}, {}]}
          numColumns={1}
          renderItem={renderItemContractItem}
        />
      </SafeAreaView>
    </View>
  )
}

export default ContractComponent;