import React, { useState } from "react"
import { Dimensions, ImageBackground, SafeAreaView, ScrollView, StyleSheet } from "react-native";
import { Avatar, TouchableOpacity, Card, Carousel, Image, Text, View, Stepper, Incubator, Dash, NumberInput, SegmentedControl, GridView, Picker } from "react-native-ui-lib"
import { image_background_noti, image_bell, image_iVay, image_leftArrow, image_noti_loan, image_slider, image_tpbank } from "../../assets/images/images";
import LinearGradient from 'react-native-linear-gradient';
import InputComponent from "../component/InputComponent";
import { colors } from "../../assets/color/color";
import FileComponent from "../component/FileComponent";
import { dataSlider, products } from "../utlis/data";




const { width, height } = Dimensions.get('screen');
const MainScreen = (props: any) => {
  const { navigation } = props;
  const [search, changeSearch] = useState('')
  const [showNoti, setShowNoti] = useState(true);

  const renderItem = (item: any) => {
    return (
      <TouchableOpacity onPress={() => item.direct && navigation.navigate(item.direct)} style={{ alignItems: "center", width: (width - 24 * 2) / 4, columnGap: 32 }} key={item.id}>
        <Image style={styles.image} source={item.pathImage} />
        <Text style={styles.text}>{item.title}</Text>
      </TouchableOpacity>
    )
  }

  return (
    <View style={{ position: "relative", flex: 1 }}>
      <Carousel
        loop
        autoplay
        autoplayInterval={10 * 1000}
        containerStyle={{
          zIndex: -1,
          height: height * 0.3,
          position: "absolute"
        }}
        style={{ zIndex: 10 }}
      >
        {dataSlider.map((item) => {
          return (
            <Image style={{ height: "100%" }} key={item.id} source={item.image} />
          )
        })}
      </Carousel>
      <View style={{ zIndex: 2, position: "absolute", flexDirection: "row", alignItems: "center", justifyContent: "space-between", marginTop: "15%", width: "100%", paddingHorizontal: 30 }}>
        <TouchableOpacity onPress={() => navigation.goBack()}>
          <Image source={image_leftArrow} style={{ width: 22, height: 22 }} />
        </TouchableOpacity>
        <Avatar onPress={() => navigation.navigate('notification')} size={40} source={image_bell} />
      </View>
      <SafeAreaView style={styles.container}>
        <View style={{ top: "-3%", zIndex: 999999999999, paddingHorizontal: "6%", position: "absolute", width: "100%" }}>
          <InputComponent onChangeText={changeSearch} text={search} placeholder="Tìm kiếm" />
        </View>
        <LinearGradient
          style={{
            borderTopRightRadius: 20,
            borderTopLeftRadius: 20,
            height: "100%",
          }}
          colors={['#F2E0FE', 'rgba(255, 255, 255, 0.8)', 'rgba(255, 255, 255, 0.82)']}
        >
          <ScrollView>
            <ScrollView
              horizontal
              showsHorizontalScrollIndicator={false}
              style={{
                paddingHorizontal: 24,
                // paddingVertical: 7,
                marginTop: 50,
                marginBottom: 10,
                flex: 1
              }}
            >
              {products.map((product) => renderItem(product))}
            </ScrollView>
            <View style={{
              paddingVertical: 20,
            }}>
              <FileComponent {...props} />
            </View>

            {showNoti && (
              <ImageBackground
                source={image_background_noti}
                style={{ width: "100%", height: 100, zIndex: 99999999 }}
              >
                <Card
                  height={100}
                  marginR-10
                  marginL-10
                  style={{ backgroundColor: "rgba(0, 32, 77, 0.01)", borderStyle: "solid", borderWidth: 0.5, borderColor: '#FEBD00', borderRadius: 10, alignItems: "flex-start", justifyContent: "center", paddingHorizontal: 15 }}
                >
                  <TouchableOpacity style={{ width: "100%" }} onPress={() => setShowNoti(false)}>
                    <Text style={{ textAlign: "right", marginTop: 8 }}>X</Text>
                  </TouchableOpacity>
                  <View style={{ flexDirection: "row", columnGap: 12, alignItems: "center" }}>
                    <View style={{ height: 46 }}>
                      <Card.Image source={image_noti_loan} style={{ height: 46, width: 46 }} />
                    </View>
                    <View style={{ rowGap: 1 }}>
                      <Card.Section contentStyle={{}} content={[{ text: 'Thông báo thanh toán khoản vay', text70BO: true, grey10: true, color: colors.colorText, }]} />
                      <Card.Section content={[{ text: 'Đại lý: Pháp Vân', text85: true, grey10: true, color: colors.text }]} />
                      <Card.Section content={[{ text: 'Ngày thanh toán: 12/03/2024 ~ 62,500,000', text85: true, grey10: true, color: colors.text }]} />
                    </View>
                  </View>
                  <TouchableOpacity style={{ width: "100%" }}>
                    <Text style={{ textAlign: "right", fontSize: 11, fontWeight: "600", color: colors.primary, fontStyle: "italic", paddingVertical: 6 }}>{'Chi tiết >>>'}</Text>
                  </TouchableOpacity>
                </Card>
              </ImageBackground>
            )}
          </ScrollView>
        </LinearGradient>
      </SafeAreaView>
    </View>
  )
}

const styles = StyleSheet.create({
  container: {
    borderTopRightRadius: 20,
    borderTopLeftRadius: 20,
    height: "100%",
    position: "absolute",
    top: height * 0.25,
  },
  image: {
    width: 38,
    height: 38
  },
  text: {
    color: colors.colorText,
    fontSize: 14,
    fontWeight: "400",
    textAlign: "center"
  }
})

export default MainScreen;