import React, { useCallback, useState } from "react";
import { ScrollView } from "react-native";
import { Chip, View } from "react-native-ui-lib";

const ScrollHoriz = (props: any) => {
  const [listStatus, setListStatus] = useState(props.listStatus)
  const handleChangeSeleced = useCallback((id: number) => {
    const newList = listStatus.map((item: any) => {
      if (item.id === id) {
        props.setChipSelected(item);
        return {
          ...item,
          selected: true
        }
      } else {
        return {
          ...item,
          selected: false
        }
      }
    })
    setListStatus(newList)
  }, [JSON.stringify(listStatus)])
  return (
    <View>
      <ScrollView
        horizontal
        showsVerticalScrollIndicator={false}
        showsHorizontalScrollIndicator={false}
        contentContainerStyle={{
          alignItems: "flex-start",
          justifyContent: "flex-start",
          columnGap: 10,
          paddingHorizontal: 24,
          marginBottom: 20
        }}
      >
        {listStatus && listStatus.map((statusContract: any) => (
          <Chip
            key={statusContract.id}
            containerStyle={{
              borderRadius: 6,
              backgroundColor: statusContract.selected ? "#F2E0FE" : "#F9FAFB",
              borderWidth: 0,
            }}

            labelStyle={{
              fontSize: 14,
              textAlign: "center",
              color: statusContract.selected ? "#62248F" : "rgba(0, 32, 77, 0.55)"
            }}
            label={statusContract.name}
            onPress={() => handleChangeSeleced(statusContract.id)} />
        ))}
      </ScrollView>
    </View>
  )
}

export default ScrollHoriz;